.Square{
    width: 100px;
    height:100px;
    background-color:  #4B495F;
    border-radius: 5px;
}

.Square:hover{
    cursor: pointer;
}
.not-allowed{
    cursor: not-allowed !important;
}
.circle-won{
    background-color: #DD7F9F;
}
.cross-won{
    background-color: rgb(63,167,240);
}