.main-div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 5vh;
}

.water-background{
  background-color: #4B495F;
}
.game-heading{
  padding: 5px 20px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

.square-wrapper{

display: grid;
grid-template-columns: 1fr 1fr 1fr;
row-gap: 10px;
column-gap: 10px;
}

.move-detection{
  display: flex;
  justify-content: space-between;
  text-align: center;
  font-family: sans-serif;
  font-size: 18px;
  width: 400px;
}

.left , .right{
  height: 40px;
  width: 120px;
  background-color:#4B495F;
  padding-top: 10px;
}

.left{
  border-bottom-left-radius: 30px;
  border-top-right-radius: 30px;
}

.right{
  border-bottom-left-radius: 30px;
  border-top-right-radius: 30px;
}

.finished-state{
  text-align: center;
  font-size: 30px;

}
.PlayOnline{
  background-color:#E4CA56 ;
  font-size: 40px;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  margin-top: 45vh;
}
.waiting{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.current-move-cross{
background-color: #3fa7f0 !important;
}
.current-move-circle{

  background-color: #DD7f9f !important;
}